import React, { useEffect, useState } from "react";
import axios from "axios";
import {
  Route, Switch, useParams
} from "react-router-dom"

const App = () => {
  const [ newBinId, setNewBinId ] = useState("");

  const createBin = async () => {
    const response = await axios.post("/api/bins/new");
    setNewBinId(response.data.id);
  };

  const formatBinUrl = () => {
    return newBinId ? `reqbox.dylan-s-jones.com/api/bins/${newBinId}` : "";
  }

  const formatBinViewUrl = () => {
    return newBinId ? `reqbox.dylan-s-jones.com/view/${newBinId}` : "";
  }

  return (
    <div>
      <header><h1>REQBOX</h1></header>
      <Switch>
        <Route path="/view/:id">
          <Requests />
        </Route>

        <Route path="/">
          <div class="content">
            <h3>Inspect HTTP Requests</h3>
            <p>REQBOX is a RequestBin clone that gives you a URL that will collect requests made to it and let you inspect them.</p>
            <p>Use REQBOX to see what your HTTP client is sending or to inspect and debug webhook requests.</p>
            <button onClick={createBin} class="create-Btn">CREATE BOX</button>
            <div class="box-url">
              <p>BOX URL:</p>
              <p class="box-url-display">{formatBinUrl()}</p>
              <p>VIEW BOX URL:</p>
              <p class="box-url-display">{formatBinViewUrl()}</p>
            </div>
          </div>
        </Route>
      </Switch>
    </div>
  );
};

const Requests = () => {
  const [ requests, setRequests ] = useState([]);
  const id = useParams().id;

  useEffect(() => {
    axios.get(`/api/bins/${id}/view`).then(res => {
      setRequests(res.data.requests);
    });
  }, [id]);

  console.log("REQUESTS:", requests);
  return (
    <div class="content">
      <h3>BIN: {id}</h3>
      <div class="request-list">
      {requests.map((request, index) => {
        return (
          <div key={index} class="request-container">
            <p><b>METHOD:</b> {request.method}</p>
            <p><b>CREATED AT:</b> {request.createdAt}</p>
            <h3>HEADERS:</h3>
            <p>{JSON.stringify(request.headers, null, 2)}</p>
            <h3>RAW BODY: </h3>
            <p>{JSON.stringify(request.body, null, 2) || "N/A"}</p>
          </div>
        );
      })}
      </div>
    </div>
  );
};

export default App;
